.main_container{
    width: 100%;
    height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;
}
.inner_container{
width: 30%;
height: auto;
background-color: white;
display: flex;
justify-content: center;
border-radius: 2rem;
box-shadow: rgba(0, 0, 0, 0.24) 0px 3px 8px;
}
.inner_container form{
width: 90%;
height: auto;
display: flex;
flex-direction: column;
gap: 2rem;
padding: 2rem 0;
}

.form_group{
    display: flex;
    justify-content: space-between;
    gap:2rem;
}
.form_group input{
    outline: none;
    border: none;
    border-radius: 2px;
    padding:0.5rem 0;
    box-shadow: rgba(0, 0, 0, 0.02) 0px 1px 3px 0px, rgba(27, 31, 35, 0.15) 0px 0px 0px 1px;}
.btn{
    margin:0 auto;
    width: 10rem;
    outline: none;
    border:none;
    padding: 1rem;
    color: white;
    border-radius: 0.2rem;
    background-color: rgb(83, 83, 249);
    cursor: pointer;
}
.btn:hover{
    background-color: rgb(101, 101, 246);
}